<script>
import asideComponent from "@/components/asideComponent.vue";
import navComponent from "@/components/navComponent.vue";
import svgLoadingComponent from "@/components/svgLoadingComponent.vue";

//***************
//import { HubConnectionBuilder } from "@microsoft/signalr";

export default {
  created() {},

  mounted() {
    this.conn = new WebSocket("ws://localhost:58316/ws");
    //  this.conn = new WebSocket("ws://mail:82/ws");
    /*this.conn.onopen =  (event)=> {
   
 }*/

    this.conn.onmessage = (event) => {
      let scannedImage = event.data;

      let mgs = JSON.parse(scannedImage);
      this.imagesscantest = mgs;

      var ind = this.imagesscantest.index;
      if (ind == 1) {
        this.keyid = this.imagesscantest.keyid;
      } else {
        //5/9/2022 this.imagesToSend=[]

        for (var i = 0; i < mgs["image"].length; i++) {
          this.indexOfimagesToShow++;
          this.imagesToSend.push({
            baseAs64: mgs["image"][i],
            index: this.indexOfimagesToShow,
          });
        }
      }
    };

    var date = new Date();

    var month = date.getMonth() + 1;
    var day = date.getDate();

    if (month < 10) month = "0" + month;
    if (day < 10) day = "0" + day;

    document
      .querySelector("#handleScrollForAyoubTest")
      .addEventListener("scroll", this.handleScroll);
    this.date_from = date.getFullYear() + "-" + month + "-" + day;
    this.date_to = date.getFullYear() + "-" + month + "-" + day;

    this.my_user_id = localStorage.getItem("AY_LW");
    this.my_department_id = localStorage.getItem("chrome");
    this.roles = localStorage.getItem("Az07");

    this.GetSentMail();

    this.GetAllmail_cases();
    this.GetAllClassifications();
    this.GetAllDepartments();
    this.GetAllSides();
    this.GetAllMeasures();
  },

  watch: {
    mailType: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;

      if (this.mailType == 0 || this.mailType == 2) {
        this.report_button = true;
      } else {
        this.report_button = false;
      }
      this.GetSentMail();
    },
    date_from: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },
    date_to: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },
    mail_id: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },
    general_incoming_number: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },

    summary: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },
    departmentIdSelected: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },

    sideIdSelected: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },

    measureIdSelected: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },
    classificationIdSelected: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },

    mail_caseIdSelected: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },

    by_date_of_reply: function() {
      this.senders = [];
      this.show_senders_mail = "";
      this.replies = [];
      this.departmentflag = 0;
      this.departmentName = "";
      this.page_num = 1;
      this.GetSentMail();
    },

    show_senders_mail: function() {
      // this.senders = [];
      // this.show_senders_mail = "";
      // this.page_num = 1;
      // this.GetSentMail();
    },
  },

  components: {
    asideComponent,
    navComponent,
    svgLoadingComponent,
  },

  data() {
    return {
      roles: [],
      by_date_of_reply: false,
      general_incoming_number: "",

      imagesToSend: [],
      indexOfimagesToShow: 0,

      show_senders_mail: "",
      senders: [],
      to_test_print: false,

      testimage: "",
      indextotest: 0,

      show_images: [],
      show_images_model: false,

      total_of_transaction: 0,
      my_user_id: "",
      my_department_id: "",

      inboxMails: [],

      mail_id: "",

      classifications: [],
      classificationselect: false,
      classificationNameSelected: "",
      classificationIdSelected: "",

      departments: [],
      departmentselect: false,
      departmentNameSelected: "",
      departmentIdSelected: "",
      departmentName: "",
      departmentflag: 0,

      sides: [],
      sideselect: false,
      sideNameSelected: "",
      sideIdSelected: "",

      measures: [],
      measureselect: false,
      measureNameSelected: "",
      measureIdSelected: "",

      mail_cases: [],
      mail_caseselect: false,
      mail_caseNameSelected: "",
      mail_caseIdSelected: "",

      mailType: 0,

      summary: "",

      filter: false,
      loading: false,
      screenFreeze: false,
      there_are_no_documents: false,

      date_from: "",
      date_to: "",

      page_size: 11,
      page_num: 1,

      mailId_to_get_mail_by_id: "",
      my_department_id_to_get_mail_by_id: "",
      to_test_passing_mail_type_to_get_mail_by_id: "",
      sends_id_to_get_mail_by_id: "",
      mangment_sender_to_get_mail_by_id: "",
      replies: [],
      reply_to_add: "",
      report_button: true,

      reply_doc_number: 0,
      reply_total_of_doc: 0,

      reply_image_of_doc: "",
      reply_id_of_doc: "",
      reply_image_to_print_n: [],

      reply_image_to_print_n_model: false,
      show_current_reply_image_to_for_bigger_screen_model: false,

      id_reply_image: "",
    };
  },

  computed: {
    filterByTerm() {
      return this.sides.filter((side) => {
        return side.section_Name.toLowerCase().includes(this.sideNameSelected);
      });
    },
  },

  methods: {
    handleScroll() {
      const obj = document.querySelector("#handleScrollForAyoubTest");
      if (obj.scrollTop === obj.scrollHeight - obj.offsetHeight) {
        if (this.inboxMails.length < this.total_of_transaction) {
          this.PushNewSentMail();
        }
      }
    },

    PushNewSentMail() {
      this.page_num = this.page_num + 1;

      this.senders = [];
      this.show_senders_mail = "";

      this.screenFreeze = true;
      this.loading = true;
      this.$http.mailService
        .sent(
          this.my_user_id,
          this.mailType,
          this.my_department_id,
          this.date_from,
          this.date_to,
          this.by_date_of_reply,
          this.mail_id,
          this.general_incoming_number,
          this.summary,
          this.departmentIdSelected,
          this.sideIdSelected,
          this.measureIdSelected,
          this.classificationIdSelected,
          this.mail_caseIdSelected,
          this.page_num,
          this.page_size
        )
        .then((res) => {
          for (let index = 0; index < res.data.mail.length; index++) {
            const element = res.data.mail[index];
            this.inboxMails.push(element);
          }

          setTimeout(() => {
            this.screenFreeze = false;
            this.loading = false;
          }, 300);
        })
        .catch((err) => {
          setTimeout(() => {
            this.screenFreeze = false;
            this.loading = false;
            console.log(err);
          }, 100);
        });
    },

    GetMailsToPrint() {
      this.$router.push({
        name: "Sent_report",
        params: {
          user_id: this.my_user_id,
          mail_type: this.mailType,
          department_Id: this.my_department_id,
          from: this.date_from,
          to: this.date_to,
          date_of_reply: this.by_date_of_reply,
          mail_id: this.mail_id,
          g_in_num: this.general_incoming_number,
          summary: this.summary,
          department_id_selected: this.departmentIdSelected,
          side_selected: this.sideIdSelected,
          measure_selected: this.measureIdSelected,
          classifaction_selected: this.classificationIdSelected,
          mail_caseid: this.mail_caseIdSelected,
          page_num: 1,
          page_size: 1000000,
        },
      });
    },

    Next_prevent_GetResources_ById(x) {
      if (x == "next") {
        this.reply_doc_number++;
      } else {
        this.reply_doc_number--;
      }

      this.screenFreeze = true;
      this.loading = true;
      this.$http.documentService
        .GetResources_ById(this.id_reply_image, this.reply_doc_number)
        .then((res) => {
          this.show_current_reply_image_to_for_bigger_screen_model = true;
          this.reply_total_of_doc = res.data.total;

          this.reply_image_of_doc = res.data.date[0].path;
          this.reply_id_of_doc = res.data.date[0].id;

          setTimeout(() => {
            this.screenFreeze = false;
            this.loading = false;
          }, 200);
        })
        .catch((err) => {
          this.screenFreeze = false;
          this.loading = false;
          console.log(err);
        });
    },

    GetResources_ById(id) {
      this.id_reply_image = id;

      this.reply_doc_number = 1;
      this.reply_image_of_doc = [];
      this.reply_id_of_doc = "";
      this.reply_total_of_doc = "";

      this.screenFreeze = true;
      this.loading = true;
      this.$http.documentService
        .GetResources_ById(id, this.reply_doc_number)
        .then((res) => {
          this.show_current_reply_image_to_for_bigger_screen_model = true;
          this.reply_total_of_doc = res.data.total;

          this.reply_image_of_doc = res.data.date[0].path;
          this.reply_id_of_doc = res.data.date[0].id;

          setTimeout(() => {
            this.screenFreeze = false;
            this.loading = false;
          }, 200);
        })
        .catch((err) => {
          this.screenFreeze = false;
          this.loading = false;
          console.log(err);
        });
    },

    reply1() {
      var link = document.getElementById("a3");
      var mailId_to_get_mail_by_id = this.mailId_to_get_mail_by_id;
      var sends_id = this.sends_id;
      var my_department_id_to_get_mail_by_id = this
        .my_department_id_to_get_mail_by_id;
      var keyid = this.keyid;

      var timeout;
      window.addEventListener("blur", function(e) {
        window.clearTimeout(timeout);
      });

      timeout = window.setTimeout(function() {
        window.location = "http://mail/scanner_app/Setup1.msi";
      }, 1000);

      link.href =
        "SScaner:flag=0" +
        "userId=" +
        localStorage.getItem("AY_LW") +
        "mId=" +
        mailId_to_get_mail_by_id +
        "send_ToId=" +
        sends_id +
        "to=" +
        my_department_id_to_get_mail_by_id +
        "keyid=" +
        keyid;
    },

    print_image() {
      this.to_test_print = true;
      this.$http.mailService
        .PrintOrShowDocument(
          Number(this.mailId_to_get_mail_by_id),
          Number(localStorage.getItem("AY_LW")),
          Number(this.from_reply_or_general)
        )
        .then((res) => {
          setTimeout(() => {
            console.log(res);
            this.loading = false;
            this.screenFreeze = false;
          }, 500);
        })
        .catch((err) => {
          setTimeout(() => {
            this.loading = false;
            this.screenFreeze = false;
          }, 500);
          console.log(err);
        });
    },

    show_reply_images(index, plase) {
      this.from_reply_or_general = plase;

      this.screenFreeze = true;
      this.loading = true;

      this.$http.mailService
        .PrintOrShowDocument(
          Number(this.mailId_to_get_mail_by_id),
          Number(localStorage.getItem("AY_LW")),
          2
        )
        .then((res) => {
          this.show_images = [];
          this.indextotest = 0;

          this.show_images = this.replies[index].resources;

          this.testimage = this.show_images[0].path;

          setTimeout(() => {
            this.show_images_model = true;
            this.screenFreeze = false;
            this.loading = false;
          }, 300);
        })
        .catch((err) => {
          setTimeout(() => {
            this.loading = false;
            this.screenFreeze = false;
          }, 500);
          console.log(err);
        });
    },

    scanToJpg() {
      scanner.scan(this.displayImagesOnPage, {
        output_settings: [
          {
            type: "return-base64",
            format: "jpg",
          },
        ],
      });
    },

    displayImagesOnPage(successful, mesg, response) {
      if (!successful) {
        // On error
        return;
      }

      if (
        successful &&
        mesg != null &&
        mesg.toLowerCase().indexOf("user cancel") >= 0
      ) {
        // User cancelled.
        return;
      }

      var scannedImages = scanner.getScannedImages(response, true, false); // returns an array of ScannedImage
      for (
        var i = 0;
        scannedImages instanceof Array && i < scannedImages.length;
        i++
      ) {
        var scannedImage = scannedImages[i];
        // this.processScannedImage(scannedImage);
        this.indexOfimagesToShow++;
        this.imagesToSend.push({
          baseAs64: scannedImage.src,
          index: this.indexOfimagesToShow,
        });

        // if (this.imagesToSend.length > 0) {
        // this.testimageToSend = this.imagesToSend[0].baseAs64;
        // this.ButtonUploadImagesMail = true;
        // }
      }

      // this.UploadImagesMail()

      // if (this.mailType == 1) {
      //   this.to_test_passing_mail_type = 1;
      // }
      // if (this.mailType == 2) {
      //   this.to_test_passing_mail_type = 2;
      // }
      // if (this.mailType == 3) {
      //   this.to_test_passing_mail_type = 3;
      // }

      // setTimeout(() => {
      //   this.GetSentMailById();
      // }, 1000);
    },

    AddReply() {
      this.screenFreeze = true;
      this.loading = true;

      var ReplyViewModel = {
        userId: Number(localStorage.getItem("AY_LW")),
        mailId: Number(this.mailId_to_get_mail_by_id),
        send_ToId: Number(this.sends_id),
        from: Number(1),
        reply: {
          mail_detail: this.reply_to_add,
          To: Number(this.my_department_id_to_get_mail_by_id),
        },
        file: {
          list: this.imagesToSend,
        },
      };
      this.$http.mailService
        .NewAddReply(ReplyViewModel)
        .then((res) => {
          setTimeout(() => {
            console.log(res);
            // this.documentSection = true;
            // this.proceduresSection = true;

            this.loading = false;
            this.screenFreeze = false;

            this.reply_to_add = "";

            this.getMailById();
            this.imagesToSend = [];

            for (let index = 0; index < this.senders.length; index++) {
              if (this.senders[index].send_ToId == this.sends_id) {
                if (this.senders[index].flag == 4) {
                  this.senders[index].flag = 5;
                  this.senders[index].state = "   ";
                }
              }
            }

            // this.GetReplyByDepartment(
            //   this.replyByDepartmenId,
            //   this.sends_id,
            //   this.departmentName
            // );
          }, 500);
        })
        .catch((err) => {
          setTimeout(() => {
            this.loading = false;
            this.screenFreeze = false;
          }, 500);
          console.log(err);
        });
    },

    to_pass_data_to_get_mail_by_id(
      my_department_id_to_get_mail_by_id,
      sends_id,
      departmentName,
      flag
    ) {
      this.departmentflag = 0;
      this.my_department_id_to_get_mail_by_id = my_department_id_to_get_mail_by_id;
      this.sends_id = sends_id;
      this.departmentName = departmentName;
      this.departmentflag = flag;

      this.getMailById();
    },

    getMailById() {
      this.reply_to_add = "";
      this.imagesToSend = [];

      this.$http.mailService
        .GetInboxMailById(
          this.mailId_to_get_mail_by_id,
          this.my_department_id_to_get_mail_by_id,
          this.to_test_passing_mail_type_to_get_mail_by_id
        )
        .then((res) => {
          this.department_Id = res.data.mail.department_Id;
          this.replies = res.data.list;

          this.consignees = res.data.actionSenders;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    show_senders(id, mail_type, number) {
      this.screenFreeze = true;
      this.loading = true;
      this.mailId_to_get_mail_by_id = id;
      this.to_test_passing_mail_type_to_get_mail_by_id = mail_type;

      this.replies = [];
      this.departmentflag = 0;

      this.$http.mailService
        .show_senders(id)
        .then((res) => {
          this.show_senders_mail = number;
          this.senders = res.data;

          setTimeout(() => {
            this.screenFreeze = false;
            this.loading = false;
          }, 300);
        })
        .catch((err) => {
          setTimeout(() => {
            this.screenFreeze = false;
            this.loading = false;
            console.log(err);
          }, 100);
        });
    },

    previousImage() {
      if (this.indextotest > 0) {
        this.indextotest--;
        this.testimage = this.show_images[this.indextotest].path;
      }
    },

    nextImage() {
      if (this.indextotest < this.show_images.length - 1) {
        this.indextotest++;
        this.testimage = this.show_images[this.indextotest].path;
      }
    },

    GetAllDocuments(id, plase) {
      this.from_reply_or_general = plase;
      this.screenFreeze = true;
      this.loading = true;
      this.mailId_to_get_mail_by_id = id;
      this.$http.mailService
        .GetAllDocuments(id, Number(localStorage.getItem("AY_LW")))
        .then((res) => {
          this.show_images = res.data;

          this.testimage = this.show_images[0].path;

          setTimeout(() => {
            this.show_images_model = true;
            this.screenFreeze = false;
            this.loading = false;
          }, 300);
        })
        .catch((err) => {
          this.loading = false;
          this.there_are_no_documents = true;
          setTimeout(() => {
            this.screenFreeze = false;
            this.there_are_no_documents = false;
            console.log(err);
          }, 700);
        });
    },

    GetSentMail() {
      this.senders = [];
      this.show_senders_mail = "";

      this.screenFreeze = true;
      this.loading = true;
      this.inboxMails = [];
      this.$http.mailService
        .sent(
          this.my_user_id,
          this.mailType,
          this.my_department_id,
          this.date_from,
          this.date_to,
          this.by_date_of_reply,
          this.mail_id,
          this.general_incoming_number,
          this.summary,
          this.departmentIdSelected,
          this.sideIdSelected,
          this.measureIdSelected,
          this.classificationIdSelected,
          this.mail_caseIdSelected,
          this.page_num,
          this.page_size
        )
        .then((res) => {
          this.inboxMails = res.data.mail;
          this.total_of_transaction = res.data.total;
          setTimeout(() => {
            this.screenFreeze = false;
            this.loading = false;
          }, 300);
        })
        .catch((err) => {
          setTimeout(() => {
            this.screenFreeze = false;
            this.loading = false;
            console.log(err);
          }, 100);
        });
    },

    GetAllDepartments() {
      this.$http.mailService
        .AllDepartments()
        .then((res) => {
          this.departments = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    selectdepartment(id, name) {
      this.departmentNameSelected = name;
      this.departmentIdSelected = id;
    },

    GetAllSides() {
      this.$http.mailService
        .AllSides()
        .then((res) => {
          this.sides = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    selectsides(id, name) {
      this.sideNameSelected = name;
      this.sideIdSelected = id;
    },

    GetAllMeasures() {
      this.$http.mailService
        .AllMeasures()
        .then((res) => {
          this.measures = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    selectmeasure(id, name) {
      this.measureNameSelected = name;
      this.measureIdSelected = id;
    },

    GetAllmail_cases() {
      this.$http.mailService
        .AllStateSent()
        .then((res) => {
          this.mail_cases = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    select_mail_case(id, name) {
      this.mail_caseNameSelected = name;
      this.mail_caseIdSelected = id;
    },

    GetAllClassifications() {
      this.$http.mailService
        .AllClassifications()
        .then((res) => {
          this.classifications = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    selectClassification(id, name) {
      this.classificationNameSelected = name;
      this.classificationIdSelected = id;
    },

    print_report() {
      this.$router.push({
        name: "Statistical_report",
        params: {
          dateFrom: this.date_from,
          dateTo: this.date_to,
          mailtype: this.mailType,
        },
      });
    },

    // GetMailsToPrint() {
    //   this.$router.push({
    //     name: "Sent_report",
    //     params: {
    //       dateFrom: this.date_from,
    //       dateTo: this.date_to,
    //       mailtype: this.mailType,
    //     },
    //   });
    // },
  },
};
</script>

